	setup() {
		const output = ref("0");
		const sign = ref("");
		const numberCache = ref(0);
		const input = function(event: MouseEvent) {
			const button = event.target as HTMLButtonElement;
			const input = button.textContent!;
			if (output.value.length === 16) {
				return;
			}
			if (output.value === "0") {
				if (input !== ".") {
					output.value = input;
				} else {
					output.value += input;
				}
				return;
			}
			if (output.value.indexOf(".") >= 0 && input === ".") {
				return;
			}
			output.value += input;
		}
		return {
			output,
			sign,
			numberCache,
			input
		}
	}
